import React from 'react'

const Search = ({ searchVar, handleClick }) => {
    return (
        searchVar.map(person => {
            return <p key={person.name}>{person.name} {person.number} <button onClick={handleClick} value={person.id} name={person.name} key={person.id}>Delete</button></p>
        })
    );
}

export default Search;