import React, { useEffect, useState } from 'react'
import Search from './Search';
import Filter from './Filter';
import PersonForm from './PersonForm';
import PersonService from './services/persons';
import Notification from './Notification'

const App = () => {
  const [ persons, setPersons ] = useState([]) 
  const [ call, setCall ] = useState(new Date()) 
  const [errorMessage, setErrorMessage] = useState();
  const [successMessage, setSuccessMessage] = useState();

  useEffect(() => {
     PersonService
        .getAll()
          .then(initialData => {
            setPersons(initialData)
          })
  }, [call])
  
  const [ newName, setNewName ] = useState('')
  const [ number, setNewNumber ] = useState('')
  const [ search, setSearch ] = useState('')

  
  const handleNameChange = (event) => {
      setNewName(event.target.value);
  }
  
  const handleNumberChange = (event) => {
      setNewNumber(event.target.value);
  }
  
  const handleSearchChange = (event) => {
    setSearch(persons.filter(person => (person.name.toLowerCase()).includes(event.target.value.toLowerCase())))
  }
  
  //check if search is empty or not
  let searchVar = '';
  if(search){
    searchVar = search;
  }else{
    searchVar = persons;
  }

  const handleNameSubmit = (event) => {
    event.preventDefault();
    const personObject = {
      name: newName,
      number: number
    }
    //also check if the entered name is already present 
    //let result = persons.filter(person => person.name === newName);
    let result = persons.find(person => person.name === newName);
    
    if(result != null){
      let id =  result.id;
      const changedPerson = { ...result, number: number}
      if (window.confirm(`${newName} is already added to phonebook, replace the old number with a new one?`))
      {
          PersonService
            .update(id, changedPerson)
              .then(updatedPerson => {
                setSuccessMessage(
                  `Updated Contact Number of ${newName}`
                )
                setTimeout(() => {
                  setSuccessMessage(null)
                }, 5000)
                //update the state
                setPersons(persons.map(person => person.id !== id ? person : updatedPerson))
              })
              //if promise rejected catch the error
              .catch(error => {
                  setErrorMessage(
                    `Information of ${newName} has already been removed from server`
                  )
                  setTimeout(() => {
                    setErrorMessage(null)
                  }, 5000)
              })
      }}
      else{
        PersonService
          .create(personObject)
          .then(returnedPerson => {
            setSuccessMessage(
              `Added ${newName}`
            )
            setPersons(persons.concat(returnedPerson))
            setNewName('')
            setNewNumber('')
            //set Successmessage
            
            setTimeout(() => {
              setSuccessMessage(null)
            }, 5000)
        })
    }
  }

  const handleDelClick = (event) => {
    let id = event.target.value;
    if (window.confirm(`Delete ${event.target.name} ?`)){
      PersonService
      .destroy(id)
      .then(status => {
          if(status === 204){
            console.log('status is', status)
            setCall(new Date())
            setSuccessMessage(`Information  of ${event.target.name} has been deleted.`)
          }
      })
    }
  }

  return (
    <div>
      <h2>Phonebook</h2>
      
      <Notification errorMessage={errorMessage} successMessage={successMessage}/>
      <Filter text={'filter shown with'} onChange={handleSearchChange}/>
      <PersonForm 
            handleNameSubmit={handleNameSubmit} 
            handleNameChange={handleNameChange} 
            handleNumberChange={handleNumberChange}
      />
      <h2>Numbers</h2>
      <Search searchVar={searchVar} handleClick={handleDelClick}/>
    </div>
  )
}

export default App